.arrow-down {
  width: 18px;
  height: 18px;
}

.button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.arrow-down_position {
  position: absolute;
  bottom: 10px;
  right: 10px;
  cursor: pointer;
}

.arrow-down_none {
  opacity: 0;
  display: none;
}
